import style from "../../style/document.module.sass";
import md from "markdown-it";
import mdHighlight from "markdown-it-highlightjs";
import mdFootnote from "markdown-it-footnote";
import mdContainer from "markdown-it-container";
import mdAnchor from "markdown-it-anchor";

var markdown = md({ linkify: true, typographer: true})
    .use(mdHighlight, {})
    .use(mdFootnote)
    .use(mdAnchor, {permalink: true, level: [1, 2 ,3], permalinkClass: style.anchor})
    .use(mdContainer, "triggered", {render: (tokens, idx) => {
        if (tokens[idx].nesting === 1) {
            // opening tag
            return `<div class="${style.triggered}">\n`;
        } else {
            // closing tag
            return '</div>\n';
        }
    }})
    .use(mdContainer, "spoiler", {render: (tokens, idx) => {
        if (tokens[idx].nesting === 1) {
            // opening tag
            return `<span class="${style.spoiler2}">\n`;
        } else {
            // closing tag
            return '</span>\n';
        }
    }});;

export function Document(props) {
    var src = (Array.isArray(props.children) ? props.children[0] : props.children) || "";

    var render = {__html: markdown.render(src)};

    return (
        <div className={style.document+" "+style.markdownModest} dangerouslySetInnerHTML={render}/>
    );
}